label {
  font-family: "Helvetica";
}

.position {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.cardGreen {
  position: relative;
  width: 920px;
  height: 200px;
  background: linear-gradient(90deg, #cff37d 0%, #7de88c 100%);
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 10px;
  margin: 10px;
}

.cardRed {
  position: relative;
  width: 920px;
  height: 200px;
  background: linear-gradient(90deg, #ffb199 0%, #ff0844 100%);
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 10px;
  margin: 10px;
}

.dino {
  position: absolute;
  left: 1.74%;
  right: 92.17%;
  top: 139px;
  bottom: 0%;
  width: 70px;
  height: 60px;
}

.asteroid1 {
  position: absolute;
  width: 71px;
  height: 72px;
  left: 122px;
  top: 28px;
}

.asteroid2 {
  position: absolute;
  width: 218px;
  height: 118px;
  left: 43px;
}

.asteroid3 {
  position: absolute;
  width: 485px;
  height: 115px;
  left: 5px;
}

.name {
  position: absolute;
  width: 500px;
  height: 32px;
  left: 325px;
  top: 30px;
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
  text-decoration-line: underline;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  position: absolute;
  width: 289px;
  height: 88px;
  left: 325px;
  top: 72px;
}

.date {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding: 0px;
  position: static;
  width: 289px;
  height: 24px;
  left: 0px;
  top: 0px;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 8px 0px;
}

.distance {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding: 0px;
  position: static;
  width: 289px;
  height: 24px;
  left: 0px;
  top: 32px;
  flex: none;
  order: 1;
  flex-grow: 0;
  margin: 8px 0px;
}

.size {
  position: static;
  height: 24px;
  left: 0px;
  right: 232px;
  top: calc(50% - 24px / 2);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 0px;
}

.rate {
  position: absolute;
  width: 80px;
  height: 20px;
  left: 774px;
  top: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  text-align: center;
}

.rateGreen {
  position: absolute;
  width: 80px;
  height: 48px;
  left: 766px;
  top: 40px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  text-align: center;
}

.rateRed {
  position: absolute;
  width: 80px;
  height: 48px;
  left: 775px;
  top: 40px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  text-align: center;
}

.button {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 16px;
  position: absolute;
  width: 137px;
  height: 30px;
  left: 720px;
  top: 96px;
  background: #186dd6;
  border-radius: 24px;
}

.destroy {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  flex: none;
  order: 1;
  flex-grow: 0;
  margin: 4px;
  background: transparent;
  border: transparent;
}
